import Picture from '../picture/picture';
import Pixel from '../pixelPosition/pixelPosition';
import Preview from '../preview/preview';
import { pointerPosition, drawPixels, drawPicture } from '../../utils/canvasUtils';
import ToolsManager from '../toolsManager/toolManager';
import Shortcuts from '../shortcuts/shortcuts';
import { download } from '../download';
import { initOauth } from '../oauth';

export default class Canvas {
  constructor(size) {
    [this.ctx] = $('#canvas');
    this.canvas = this.ctx.getContext('2d');
    [this.preview] = $('#preview');
    this.frame = document.querySelector('.frame-canvas');
    this.currentFrame = 0;
    this.pictures = [];
    this.size = size;
    this.setCanvasSize(size);
  }

  setCanvasSize(size) {
    this.scale = Math.floor(720 / size);
    this.ctx.width = this.scale * size;
    this.ctx.height = this.scale * size;
  }

  init() {
    this.loadFromLocalStorage();

    const pixel = new Pixel();
    const preview = new Preview(this.pictures, this.preview, 1, this.size);
    preview.animation();

    const color = {
      primary: '#000000',
      secondary: '#ffffff',
    };

    const tools = new ToolsManager(color);
    tools.colorSelect();

    const shortcuts = new Shortcuts();

    $(document).bind('keyup', (e) => {
      const rez = shortcuts.check(e.which);
      if (rez.type === 'tool') {
        tools.selectTool(rez.tool);
      } else if (rez.type === 'action') {
        this.action(rez.tool);
      }
    });

    this.ctx.onmousedown = (e) => {
      const pos = pointerPosition(e, this.scale);
      const drawn = tools.startAction(pos, this.pictures[this.currentFrame], e.which === 3);
      drawPixels(this.canvas, drawn, this.scale);
    };
    document.onmouseup = () => {
      const drawn = tools.finishAction();
      this.pictures[this.currentFrame].draw(drawn);
      drawPicture(this.canvas, this.pictures[this.currentFrame], this.scale);
      this.updateFrame(this.frame, this.currentFrame);
    };
    this.ctx.addEventListener('mousemove', (e) => {
      const pos = pointerPosition(e, this.scale);
      pixel.showPixel(pos);
      drawPicture(this.canvas, this.pictures[this.currentFrame], this.scale);
      const drawn = tools.action(pos);
      drawPixels(this.canvas, drawn, this.scale);
    });

    this.ctx.addEventListener('contextmenu', (e) => {
      e.preventDefault();
    });

    $('.select-size').bind('change', (e) => {
      const size = e.target.value;
      this.pictures.forEach((el) => {
        el.resize(size);
      });
      this.size = size;
      this.setCanvasSize(size);
      preview.setCanvasSize(size);
      drawPicture(this.canvas, this.pictures[this.currentFrame], this.scale);
      $('.frame-canvas').each((index, el) => this.updateFrame(el, index));
    });

    $('.tool').bind('click', (e) => {
      this.action(e.target.dataset.tool);
    });

    $('#save-button').on('click', () => {
      download(this.pictures, preview.fps, this.size);
    });

    document.addEventListener('keypress', (e) => {
      if (e.keyCode === 13) {
        if (!document.fullscreenElement) {
          this.preview.requestFullscreen();
        } else if (document.exitFullscreen) {
          document.exitFullscreen();
        }
      }
    }, false);

    initOauth(this, preview);
  }

  updateFrame(frame, index) {
    const scale = 96 / this.size;
    const frameCanvas = frame.getContext('2d');
    frameCanvas.clearRect(0, 0, 96, 96);
    drawPicture(frameCanvas, this.pictures[index], scale);
    this.saveToLocalStorage();
  }

  setCurrentFrame(frame) {
    this.frame = frame.querySelector('.frame-canvas');
    this.currentFrame = this.constructor.detectDataIndex(frame);
    if (!this.pictures[this.currentFrame]) {
      const picture = new Picture(this.size, this.size, '#FFFFFF');
      this.pictures.push(picture);
      drawPicture(this.canvas, picture, this.scale);
    }
    this.updateFrame(this.frame, this.currentFrame);
  }

  loadData() {
    drawPicture(this.canvas, this.pictures[this.currentFrame], this.scale);
  }

  deleteData(frame) {
    const dataIndex = this.constructor.detectDataIndex(frame);
    this.pictures.splice(dataIndex, 1);
  }

  duplicateData(frame) {
    const dataIndex = this.constructor.detectDataIndex(frame);
    const picture = this.pictures[dataIndex];
    this.pictures.splice(dataIndex, 0, picture.copy());
  }

  action(tool) {
    if (tool === 'rotate') {
      this.pictures[this.currentFrame].rotate();
      drawPicture(this.canvas, this.pictures[this.currentFrame], this.scale);
    } else if (tool === 'mirrorY') {
      this.pictures[this.currentFrame].mirror(false);
      drawPicture(this.canvas, this.pictures[this.currentFrame], this.scale);
    } else if (tool === 'mirrorX') {
      this.pictures[this.currentFrame].mirror(true);
      drawPicture(this.canvas, this.pictures[this.currentFrame], this.scale);
    }
    this.updateFrame(this.frame, this.currentFrame);
  }

  swapElements(index, swapIndex) {
    if (index !== swapIndex) {
      const temp = this.pictures[index - 1];
      this.pictures[index - 1] = this.pictures[swapIndex - 1];
      this.pictures[swapIndex - 1] = temp;
      $('.frame-canvas').each((i, el) => this.updateFrame(el, i));
    }
  }

  static detectDataIndex(frame) {
    return frame.querySelector('.canvas-background').dataset.index;
  }

  saveToLocalStorage() {
    localStorage.setItem('size', this.size);
    localStorage.setItem('pictures', JSON.stringify(this.pictures));
  }

  loadFromLocalStorage() {
    const size = +localStorage.getItem('size');
    const pictures = JSON.parse(localStorage.getItem('pictures'));
    if (size) {
      this.size = size;
      this.setCanvasSize(size);
      for (let i = 0; i < pictures.length; i += 1) {
        const picture = new Picture(this.size, this.size, '#FFFFFF');
        picture.colorswap(pictures[i].pixels);
        this.pictures.push(picture);
      }
      drawPicture(this.canvas, this.pictures[0], this.scale);
      $('.frame-canvas').each((index, el) => this.updateFrame(el, index));
      const frame = document.querySelector('.app-frames__item');
      this.setCurrentFrame(frame);
      $('.select-size').val(this.size);
    } else {
      const picture = new Picture(this.size, this.size, '#FFFFFF');
      this.pictures.push(picture);
      drawPicture(this.canvas, picture, this.scale);
    }
  }
}
