/* eslint-disable no-restricted-syntax */
export default class Picture {
  constructor(width, height, color) {
    this.width = width;
    this.height = height;
    this.initColor = color;
    this.pixels = this.empty(color);
  }

  empty(color) {
    return new Array(this.width * this.height).fill(color);
  }

  pixel(x, y) {
    return this.pixels[x + y * this.width];
  }

  draw(pixels) {
    for (const { x, y, color } of pixels) {
      if (x >= 0 && x < this.width && y >= 0 && y < this.height) {
        this.pixels[x + y * this.width] = color;
      }
    }
  }

  colorswap(pixels) {
    this.pixels = pixels;
  }

  setSize(size) {
    this.width = size;
    this.height = size;
  }

  resize(size) {
    const prevSize = this.width;
    const pixels = new Array(size ** 2).fill(this.initColor);
    const len = Math.min(size, prevSize);
    for (let y = 0; y < len; y += 1) {
      for (let x = 0; x < len; x += 1) {
        pixels[x + y * size] = this.pixel(x, y);
      }
    }
    this.setSize(size);
    this.pixels = pixels;
  }

  copy() {
    const copy = new Picture(this.width, this.height, this.initColor);
    copy.pixels = this.pixels.slice();
    return copy;
  }

  rotate() {
    const drawn = [];
    for (let y = 0; y < this.height; y += 1) {
      for (let x = 0; x < this.width; x += 1) {
        drawn.push({ x: this.height - y - 1, y: x, color: this.pixel(x, y) });
      }
    }
    this.draw(drawn);
  }

  mirror(check) {
    const drawn = [];
    let x;
    let y;
    if (!check) {
      for (y = 0; y < this.height; y += 1) {
        for (x = 0; x < this.width; x += 1) {
          drawn.push({ x, y: this.height - y - 1, color: this.pixel(x, y) });
        }
      }
    } else {
      for (y = 0; y < this.height; y += 1) {
        for (x = 0; x < this.width; x += 1) {
          drawn.push({ x: this.width - x - 1, y, color: this.pixel(x, y) });
        }
      }
    }
    this.draw(drawn);
  }
}
