import { drawPicture } from '../../utils/canvasUtils';

export default class Preview {
  constructor(frames, ctx, fps, size) {
    this.frames = frames;
    this.ctx = ctx;
    this.fps = fps;
    this.initFpsChange();
    this.setCanvasSize(size);
    this.loadFromLocalStorage();
  }

  setCanvasSize(size) {
    this.scale = Math.floor(240 / size);
    this.ctx.width = this.scale * size;
    this.ctx.height = this.scale * size;
  }

  initFpsChange() {
    this.rangeFps = document.getElementById('fps');
    this.rangeFps.addEventListener('change', (e) => {
      const fps = e.target.value;
      document.querySelector('.fps-label').innerHTML = fps;
      this.setFps(fps);
    });
  }

  animation() {
    const previewCanvas = this.ctx.getContext('2d');
    let count = 0;
    let now;
    let then = Date.now();
    let delta;
    const that = this;

    function draw() {
      requestAnimationFrame(draw);

      now = Date.now();
      delta = now - then;

      const interval = 1000 / that.fps;

      if (delta > interval) {
        then = now - (delta % interval);

        if (!that.frames[count]) {
          count = 0;
        }
        drawPicture(previewCanvas, that.frames[count], that.scale);
        if (count === that.frames.length - 1) {
          count = 0;
        } else {
          count += 1;
        }
      }
    }

    draw();
  }

  setFps(fps) {
    this.fps = fps;
    this.saveToLocalStorage();
  }

  saveToLocalStorage() {
    localStorage.setItem('fps', this.fps);
  }

  loadFromLocalStorage() {
    const fps = localStorage.getItem('fps');
    if (fps) {
      this.fps = +fps;
      this.rangeFps.value = fps;
      document.querySelector('.fps-label').innerHTML = fps;
    }
  }
}
