import FrameFactory from './frameFactory/frameFactory';
import Canvas from './canvas/canvas';

export default class PiskelClone {
  constructor() {
    this.frameCount = 0;
  }

  init() {
    this.loadFromLocalStorage();
    this.currentFrame = document.querySelector('.app-frames__item');
    this.canvas = new Canvas(32);
    this.canvas.init();
    document.getElementById('add-frame').addEventListener('click', () => {
      this.addFrame();
    });
    this.framesList = document.getElementById('app-frames');
    this.framesList.addEventListener('click', (e) => {
      if (e.target.classList.contains('canvas-background')) {
        this.setFrame(e.target.dataset.index);
      } else if (e.target.classList.contains('delete-frame')) {
        this.deleteFrame(e);
      } else if (e.target.classList.contains('duplicate-frame')) {
        this.duplicateFrame(e);
      }
    });
    this.move();
  }

  addFrame() {
    this.currentFrame.classList.remove('selected');
    FrameFactory.draw();
    this.updateFrameCount(true);
    this.currentFrame = document.getElementsByClassName('app-frames__item')[this.frameCount - 1];
    this.currentFrame.classList.add('selected');
    this.indexing();
    this.canvas.setCurrentFrame(this.currentFrame);
    if (this.frameCount === 2) {
      document.querySelector('.delete-frame').style.display = 'block';
    }
  }

  indexing() {
    this.frames = Array.from(document.getElementsByClassName('app-frames__item'));
    this.frames.forEach((frame, i) => {
      const canvas = frame.querySelector('.canvas-background');
      const index = frame.querySelector('.frame-count');
      canvas.setAttribute('data-index', i);
      index.innerHTML = i + 1;
    });
  }

  setFrame(index) {
    const frame = document.getElementsByClassName('app-frames__item')[index];
    if (frame !== this.currentFrame) {
      this.currentFrame.classList.remove('selected');
      this.currentFrame = frame;
      this.currentFrame.classList.add('selected');
      this.canvas.setCurrentFrame(this.currentFrame);
      this.canvas.loadData();
    }
  }

  deleteFrame(e) {
    const frame = e.target.parentElement;
    this.canvas.deleteData(frame);
    if (frame === this.currentFrame) {
      frame.remove();
      this.updateFrameCount(false);
      this.currentFrame = document.getElementsByClassName('app-frames__item')[this.frameCount - 1];
    } else {
      frame.remove();
      this.updateFrameCount(false);
    }
    this.currentFrame.classList.add('selected');
    this.indexing();
    this.canvas.setCurrentFrame(this.currentFrame);
    this.canvas.loadData();
    if (this.frameCount === 1) {
      document.querySelector('.delete-frame').style.display = 'none';
    }
  }

  duplicateFrame(e) {
    const frame = e.target.parentElement;
    this.canvas.duplicateData(frame);
    this.currentFrame.classList.remove('selected');
    FrameFactory.duplicate(frame);
    this.updateFrameCount(true);
    this.indexing();
    this.currentFrame = frame.nextSibling.nextElementSibling;
    this.currentFrame.classList.add('selected');
    this.canvas.setCurrentFrame(this.currentFrame);
    this.canvas.loadData();
    this.canvas.updateFrame(this.canvas.frame, this.canvas.currentFrame);
    if (this.frameCount === 2) {
      document.querySelector('.delete-frame').style.display = 'block';
    }
  }

  move() {
    document.body.onmousedown = (e) => {
      const { target } = e;
      if (target.classList.contains('frame-count')) {
        const index = target.innerHTML;
        this.setFrame(index - 1);
        const frame = document.getElementsByClassName('app-frames__item')[index - 1];
        const clone = frame.cloneNode(true);
        frame.classList.add('move');
        clone.style.position = 'absolute';
        const y = e.pageY;
        const posX = '35px';
        let posY;

        const moveAt = (ev) => {
          posY = `${y - 80 + (ev.pageY - y)}px`;
          clone.style.left = posX;
          clone.style.top = posY;
        };

        moveAt(e);
        this.framesList.appendChild(clone);
        clone.style.zIndex = 100;
        document.onmousemove = (event) => {
          moveAt(event);
        };

        clone.onmouseup = (ev) => {
          frame.classList.remove('move');
          this.framesList.removeChild(clone);
          const elFromPoint = document.elementFromPoint(ev.pageX, ev.pageY);
          if (elFromPoint.classList.contains('frame-count')) {
            const swapIndex = elFromPoint.innerHTML;
            this.canvas.swapElements(index, swapIndex);
            this.setFrame(swapIndex - 1);
          }
          document.onmousemove = null;
          clone.onmouseup = null;
        };
      }
    };
  }

  updateFrameCount(bool) {
    if (bool) {
      this.frameCount += 1;
    } else {
      this.frameCount -= 1;
    }
    this.saveToLocalStorage();
  }

  saveToLocalStorage() {
    localStorage.setItem('frameCount', this.frameCount);
  }

  loadFromLocalStorage() {
    const frameCount = localStorage.getItem('frameCount');
    if (frameCount) {
      this.frameCount = +frameCount;
      for (let i = 1; i <= frameCount; i += 1) {
        FrameFactory.draw();
      }
    } else {
      this.frameCount = 1;
      FrameFactory.draw();
    }
    if (this.frameCount === 1) {
      document.querySelector('.delete-frame').style.display = 'none';
    }
    document.querySelector('.app-frames__item').classList.add('selected');
    this.indexing();
  }
}
