/* eslint-disable no-restricted-syntax */
export function stroke(from, to, color) {
  let [start, end] = [from, to];
  const drawn = [];
  if (Math.abs(start.x - end.x) > Math.abs(start.y - end.y)) {
    if (start.x > end.x) [start, end] = [end, start];
    const slope = (end.y - start.y) / (end.x - start.x);
    for (let { x, y } = start; x <= end.x; x += 1) {
      drawn.push({ x, y: Math.round(y), color });
      y += slope;
    }
  } else {
    if (start.y > end.y) [start, end] = [end, start];
    const slope = (end.x - start.x) / (end.y - start.y);
    for (let { x, y } = start; y <= end.y; y += 1) {
      drawn.push({ x: Math.round(x), y, color });
      x += slope;
    }
  }
  return drawn;
}

export function rectangle(from, to, color) {
  const drawn = [];
  const [start, end] = [from, to];
  const xStart = Math.min(start.x, end.x);
  const yStart = Math.min(start.y, end.y);
  const xEnd = Math.max(start.x, end.x);
  const yEnd = Math.max(start.y, end.y);
  for (let y = yStart; y <= yEnd; y += 1) {
    for (let x = xStart; x <= xEnd; x += 1) {
      if (y === yStart || y === yEnd || x === xStart || x === xEnd) {
        drawn.push({ x, y, color });
      }
    }
  }
  return drawn;
}

export function circle(from, to, color) {
  const radius = Math.sqrt(((to.x - from.x) ** 2) + ((to.y - from.y) ** 2));
  const radiusC = Math.ceil(radius);
  const drawn = [];
  for (let dy = -radiusC; dy <= radiusC; dy += 1) {
    for (let dx = -radiusC; dx <= radiusC; dx += 1) {
      const dist = Math.sqrt((dx ** 2) + (dy ** 2));
      if (dist <= radius) {
        const y = from.y + dy;
        const x = from.x + dx;
        drawn.push({ x, y, color });
      }
    }
  }
  return drawn;
}

export function bucket(pos, color, picture) {
  const around = [{ dx: -1, dy: 0 }, { dx: 1, dy: 0 }, { dx: 0, dy: -1 }, { dx: 0, dy: 1 }];
  const targetColor = picture.pixel(pos.x, pos.y);
  const drawn = [{ x: pos.x, y: pos.y, color }];
  for (let done = 0; done < drawn.length; done += 1) {
    for (const { dx, dy } of around) {
      const x = drawn[done].x + dx;
      const y = drawn[done].y + dy;
      if (x >= 0 && x < picture.width
          && y >= 0 && y < picture.height
          && picture.pixel(x, y) === targetColor
          && !drawn.some(p => p.x === x && p.y === y)) {
        drawn.push({ x, y, color });
      }
    }
  }
  return drawn;
}

export function move(from, to, picture) {
  const drawn = [];
  const xOffset = from.x - to.x;
  const yOffset = from.y - to.y;
  let posX;
  let posY;
  for (let y = 0; y < picture.height; y += 1) {
    for (let x = 0; x < picture.width; x += 1) {
      posX = x - xOffset;
      posY = y - yOffset;
      if (posX >= 0 && posX < picture.width && posY >= 0 && posY <= picture.height) {
        drawn.push({ x: posX, y: posY, color: picture.pixel(x, y) });
      }
    }
  }
  for (let y = 0; y < picture.height; y += 1) {
    for (let x = 0; x < picture.width; x += 1) {
      const check = drawn.find(el => (el.x === x && el.y === y));
      if (check === undefined) {
        drawn.push({ x, y, color: picture.initColor });
      }
    }
  }
  return drawn;
}

export function changeColor(color, value) {
  const colorHex = color.slice(1);

  const num = parseInt(colorHex, 16);

  let r = (num >> 16) + value;

  if (r > 255) r = 255;
  else if (r < 0) r = 0;

  let b = ((num >> 8) & 0x00FF) + value;

  if (b > 255) b = 255;
  else if (b < 0) b = 0;

  let g = (num & 0x0000FF) + value;

  if (g > 255) g = 255;
  else if (g < 0) g = 0;

  return `#${(g | (b << 8) | (r << 16)).toString(16)}`;
}
