/* eslint-disable no-plusplus */
import { OAuth } from 'oauthio-web';
import { createAPNG } from './download';

export function dataURLtoFile(dataurl, filename) {
  const arr = dataurl.split(',');
  const mime = arr[0].match(/:(.*?);/)[1];
  const bstr = atob(arr[1]); let n = bstr.length;
  const u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, { type: mime });
}

function uploadGoogleDrive(accessToken, canvas, preview) {
  const fileContent = createAPNG(canvas.pictures, canvas.size, preview.fps, false);
  const file = dataURLtoFile(fileContent, 'piskel.png');
  const metadata = {
    name: 'piskel.png',
    mimeType: 'image/png',
  };

  const form = new FormData();
  form.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
  form.append('file', file);

  fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id', {
    method: 'POST',
    headers: new Headers({ Authorization: `Bearer ${accessToken}` }),
    body: form,
  }).then(() => alert('Upload file to google drive successful!'));
}

export function initOauth(canvas, preview) {
  $('#google-button').on('click', (e) => {
    OAuth.initialize('TqYzWhFVR2olQY8QcFEMcUPFnXU');
    OAuth.popup('google').then((google) => {
      const accessToken = google.access_token;
      $('#upload-button').on('click', () => {
        uploadGoogleDrive(accessToken, canvas, preview);
      });
      google.get('https://people.googleapis.com/v1/people/me?personFields=names').then((data) => {
        const p = document.createElement('p');
        p.innerHTML = `Welcome, ${data.names[0].displayName}`;
        p.classList.add('zoomInLeft', 'login');
        e.target.style.display = 'none';
        e.target.parentNode.appendChild(p);
      });
    });
  });
}
