/* eslint-disable no-restricted-syntax */
export function drawPicture(dom, picture, scale) {
  const canvas = dom;
  for (let y = 0; y < picture.height; y += 1) {
    for (let x = 0; x < picture.width; x += 1) {
      canvas.fillStyle = picture.pixel(x, y);
      canvas.fillRect(x * scale, y * scale, scale, scale);
    }
  }
}

export function drawPixels(dom, pixels, scale) {
  const canvas = dom;
  for (const { x, y, color } of pixels) {
    canvas.fillStyle = color;
    canvas.fillRect(x * scale, y * scale, scale, scale);
  }
}

export function pointerPosition(e, scale) {
  const rect = e.target.getBoundingClientRect();
  const posX = Math.floor((e.clientX - rect.left) / scale);
  const posY = Math.floor((e.clientY - rect.top) / scale);
  return {
    x: posX > 0 ? posX : 0,
    y: posY > 0 ? posY : 0,
  };
}
