/* eslint-disable no-param-reassign */
import defaultShortcuts from './default.shortcuts';
import template from './shortcats.template';

export default class Shortcuts {
  constructor() {
    this.default = defaultShortcuts;
    this.setDefault();
    this.initHotkeyModal();
  }

  setDefault() {
    this.shortcuts = Object.assign({}, this.default);
  }

  check(keyCode) {
    const { tools } = this.shortcuts;
    let rez = { tool: undefined, type: undefined };
    Object.values(tools).forEach((tool) => {
      if (tool[0] === keyCode) {
        rez = { tool: tool[1], type: tool[2] };
      }
    });
    return rez;
  }

  loadShortcats() {
    const { tools } = this.shortcuts;
    Object.values(tools).forEach((tool) => {
      const key = String.fromCharCode(tool[0]);
      Shortcuts.createField(key, tool[1]);
    });
  }

  initHotkeyModal() {
    Shortcuts.drawModal();

    const modal = document.querySelector('.modal');
    const modalOverlay = document.querySelector('.modal-overlay');
    const closeButton = document.querySelector('.close-button');
    const openButton = document.querySelector('.shortcuts');
    const list = document.querySelector('.shortcat-list');

    closeButton.addEventListener('click', () => {
      modal.classList.toggle('closed');
      modalOverlay.classList.toggle('closed');
      const editing = list.querySelector('.editing');
      if (editing) {
        editing.classList.toggle('editing');
      }
    });

    openButton.addEventListener('click', () => {
      modal.classList.toggle('closed');
      modalOverlay.classList.toggle('closed');
    });

    list.addEventListener('click', (e) => {
      const item = e.target.parentNode;
      if (item.classList.contains('shortcat-item')) {
        const editing = list.querySelector('.editing');
        if (editing && editing !== item) {
          editing.classList.toggle('editing');
        }
        item.classList.toggle('editing');
      }
    });

    $(document).bind('keyup', (e) => {
      const editing = list.querySelector('.editing');
      if (editing) {
        if (e.which <= 90 && e.which >= 48) {
          this.updateShortcats(editing, e.which);
        }
      }
    });

    this.loadShortcats();
  }

  updateShortcats(editing, code) {
    const actualTool = editing.dataset.toolstr;
    const { tools } = this.shortcuts;
    Object.values(tools).forEach((tool) => {
      if (tool[0] === code && tool[1] !== actualTool) {
        this.shortcuts.tools[tool[1].toUpperCase()][0] = undefined;
        const dom = $(`*[data-toolstr="${tool[1]}"]`)[0];
        dom.querySelector('.shortcat-key').innerHTML = '???';
      } else if (tool[1] === actualTool) {
        this.shortcuts.tools[tool[1].toUpperCase()][0] = code;
      }
    });
    editing.querySelector('.shortcat-key').innerHTML = String.fromCharCode(code);
  }

  static drawModal() {
    const parrent = document.getElementById('app');
    parrent.insertAdjacentHTML('beforebegin', template);
  }

  static createField(keyCode, tool) {
    const parrent = document.querySelector('.shortcat-list');
    const li = document.createElement('li');
    li.classList.add('shortcat-item');
    li.dataset.toolstr = tool;
    const key = document.createElement('span');
    key.classList.add('shortcat-key');
    key.innerHTML = keyCode;
    const desc = document.createElement('span');
    desc.classList.add('shortcat-description');
    desc.innerHTML = `${tool} tool`;
    li.appendChild(key);
    li.appendChild(desc);
    parrent.appendChild(li);
  }
}
