import {
  stroke, rectangle, circle, bucket, move, changeColor,
} from './tools';

export default class ToolsManager {
  constructor(color) {
    this.color = color;
    this.currTool = document.querySelector('[data-tool="pencil"]');
    this.tool = 'pencil';
    this.selectedColor = this.color.primary;
    this.actionStart = false;
    this.start = false;
    this.rightClick = false;
    this.drawn = [];
    this.picture = null;
    this.drawTools = ['pencil', 'stroke', 'rectangle', 'circle'];
    this.init();
  }

  init() {
    document.querySelector('.app-tools').addEventListener('click', (e) => {
      if (e.target.classList.contains('tool')) {
        this.selectTool(e.target.dataset.tool);
      }
    });
  }

  startAction(pos, picture, rightClick) {
    this.rightClick = rightClick;
    this.actionStart = true;
    this.picture = picture;
    this.start = pos;
    const { x, y } = pos;
    this.updateColor();
    if (this.drawTools.includes(this.tool)) {
      this.drawn.push({ x, y, color: this.selectedColor });
    } else {
      switch (this.tool) {
        case 'lighten':
          this.drawn = this.lighten(pos);
          return this.drawn;
        case 'eraser':
          this.drawn = [{ x, y, color: this.picture.initColor }];
          return this.drawn;
        case 'bucket':
          this.drawn = bucket(pos, this.selectedColor, picture);
          return this.drawn;
        case 'colorswap':
          this.drawn = this.picture.empty(this.selectedColor);
          this.picture.colorswap(this.drawn);
          return [];
        case 'pipette':
          this.colorPicker(picture.pixel(x, y));
          return this.drawn;
        default:
          return [];
      }
    }
    return this.drawn;
  }

  finishAction() {
    const drawn = this.drawn.slice();
    this.drawn = [];
    this.actionStart = false;
    return drawn;
  }

  colorSelect() {
    document.querySelector('.color-select').addEventListener('input', (e) => {
      if (e.target.classList.contains('primary-color')) {
        this.color.primary = e.target.value;
      } else if (e.target.classList.contains('secondary-color')) {
        this.color.secondary = e.target.value;
      }
    });
  }

  selectTool(tool) {
    this.tool = tool;
    this.currTool.classList.remove('selected');
    [this.currTool] = $(`[data-tool="${tool}"]`);
    this.currTool.classList.add('selected');
  }

  updateColor() {
    if (this.rightClick) {
      this.selectedColor = this.color.secondary;
    } else {
      this.selectedColor = this.color.primary;
    }
  }

  action(pos) {
    if (this.actionStart) {
      switch (this.tool) {
        case 'pencil':
          return this.drawPen(pos);
        case 'lighten':
          return this.lighten(pos);
        case 'eraser':
          return this.drawEraser(pos);
        case 'stroke':
          return this.drawStroke(pos);
        case 'rectangle':
          return this.drawRectangle(pos);
        case 'circle':
          return this.drawCircle(pos);
        case 'move':
          return this.drawMove(pos);
        case 'bucket':
          return this.drawn;
        default:
          return [];
      }
    }
    return [];
  }

  drawPen(pos) {
    Array.prototype.push.apply(this.drawn, stroke(this.start, pos, this.selectedColor));
    this.start = pos;
    return this.drawn;
  }

  drawRectangle(pos) {
    this.drawn = rectangle(this.start, pos, this.selectedColor);
    return this.drawn;
  }

  drawCircle(pos) {
    this.drawn = circle(this.start, pos, this.selectedColor);
    return this.drawn;
  }

  drawStroke(pos) {
    this.drawn = stroke(this.start, pos, this.selectedColor);
    return this.drawn;
  }

  drawEraser(pos) {
    Array.prototype.push.apply(this.drawn, stroke(this.start, pos, this.picture.initColor));
    this.start = pos;
    return this.drawn;
  }

  colorPicker(color) {
    if (this.rightClick) {
      this.color.secondary = color;
      document.querySelector('.secondary-color').value = color;
    } else {
      this.color.primary = color;
      document.querySelector('.primary-color').value = color;
    }
  }

  drawMove(pos) {
    this.drawn = move(this.start, pos, this.picture);
    return this.drawn;
  }

  lighten(pos) {
    const { x, y } = pos;
    let color;
    this.drawn = [];
    if (this.rightClick) {
      color = changeColor(this.picture.pixel(x, y), -10);
    } else {
      color = changeColor(this.picture.pixel(x, y), 10);
    }
    this.drawn.push({ x, y, color });
    this.picture.draw(this.drawn);
    return [];
  }
}
