/* eslint-disable no-param-reassign */
import GIF from 'gif.js.optimized';
import UPNG from 'upng-js';
import Base64 from 'base64-js';
import { drawPicture } from '../utils/canvasUtils';

export function createCanvas() {
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');
  canvas.width = 1024;
  canvas.height = 1024;
  return ctx;
}

export function save(url, type) {
  const a = document.createElement('a');
  a.href = url;
  a.download = `animation.${type}`;
  document.body.appendChild(a);
  a.click();
  setTimeout(() => {
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  }, 0);
}

export function createGIF(frames, size) {
  const ctx = createCanvas();
  const gif = new GIF({
    workers: 2,
    quality: 10,
    width: 1024,
    height: 1024,
  });

  frames.forEach((el) => {
    drawPicture(ctx, el, 1024 / size);
    gif.addFrame(ctx, { copy: true });
  });

  gif.on('finished', (blob) => {
    const url = window.URL.createObjectURL(blob);
    save(url, 'gif');
  });

  gif.render();
}

export function createAPNG(frames, size, fps, check) {
  const ctx = createCanvas();
  const imgs = [];
  frames.forEach((el) => {
    drawPicture(ctx, el, 1024 / size);
    imgs.push(ctx.getImageData(0, 0, 1024, 1024).data);
  });
  const dels = Array(imgs.length).fill(1000 / fps);
  const apng = UPNG.encode(imgs, 1024, 1024, 256, dels);
  const data = Base64.fromByteArray(new Uint8Array(apng));
  const url = `data:image/png;base64,${data}`;
  if (check) {
    save(url, 'png');
  }
  return url;
}

export function download(frames, fps, size) {
  const type = $('.select-save').val();
  if (type === 'png') {
    createAPNG(frames, size, fps, true);
  } else if (type === 'gif') {
    createGIF(frames, size);
  }
}
